import styled from 'styled-components';
import React from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {HorizontalSpace, VerticalSpace} from "../../templates";
import {NavLink} from "react-router-dom";
import {RouteElem} from "../index";


const SidebarWrapper = styled.div`
    display: flex;
    flex-direction: column;
    background-color: #222831;
    flex: 2;
    height: 90vh;
`;

const SidebarElement = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px;
    font-size: 0.8em;
`;


function Sidebar({routes}: any) {
    return <SidebarWrapper>
        <VerticalSpace height={"25px"}/>
        {
            routes.filter((elem: RouteElem) => elem.icon && elem.sidebarTitle).map((elem: RouteElem, index: number) =>
                <NavLink to={elem.path} activeStyle={{color: "#ddd3d3"}} style={{color: "#999999"}} key={index}>
                    <SidebarElement>
                        {elem.icon ? <FontAwesomeIcon icon={elem.icon} /> : null}
                        <HorizontalSpace width={"10px"}/>
                        {elem.sidebarTitle}
                    </SidebarElement>
                </NavLink>
                )
        }
    </SidebarWrapper>
}

export default Sidebar;