import {Route} from "react-router-dom";
import React from "react";
import Sidebar from "./sidebar";
import styled from 'styled-components'
import {faChartBar, IconDefinition, faAddressBook, faMapMarked} from '@fortawesome/free-solid-svg-icons'
import Statistics from "../statistics";
import Appeals from "../appeals";
import AppealStats from "../appealStats";
import AppealDetails from "../appealDetails";

const routes: Array<RouteElem> = [
    {
        path: "/",
        exact: true,
        sidebarTitle: "Карта",
        main: () => <Statistics />,
        icon: faMapMarked,
    },
    {
        path: "/appeals",
        exact: true,
        sidebarTitle: "Статистика жалоб",
        main: () => <Appeals />,
        icon: faChartBar,
    },
    {
        exact: true,
        path: "/appealStats",
        main: () => <AppealStats />,
    },
    {
        exact: true,
        path: "/appealDetails",
        main: () => <AppealDetails />,
    },
];

export interface RouteElem{
    path: string,
    exact?: boolean,
    sidebarTitle?: string,
    main: Function,
    icon?: IconDefinition,
}



const Wrapper = styled.div`
    display: flex;
    flex-direction: row;
`;

const RouterWrapper = styled.div`
    flex: 11;
`;
function Routing() {
    return <Wrapper>
            <Sidebar routes={routes}/>
            <RouterWrapper>
                {
                    routes.map((elem, index) =>
                        <Route exact={elem.exact} key={index} path={elem.path}>
                            <elem.main />
                        </Route>
                    )
                }
            </RouterWrapper>
        </Wrapper>

}

export default Routing;